.contact {
    padding: 100px 0;
    background-color: #151b21;
    color: #f9f9f9;
  }
  
  .contact-form {
    max-width: 100%;
    margin: 0 auto;
    padding-left: 50px;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 90%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .contact-form button {
    padding: 10px 20px;
    background-color: #1e9cd9;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  @media screen and (max-width: 768px) {
    .contact {
      position: relative;
      overflow: hidden;
    }
    .contact-form {
        max-width: 100%;
        padding: 15px;
        margin: 30px;
        background-color: #2C343A;
      }
      .contact-form input,
    .contact-form textarea {
    width: 90%;
    padding: 10px;
    padding-right: -30px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  }